import { useEffect, useState } from "react";
import { isSignInWithEmailLink, signInWithEmailLink, signOut } from '@firebase/auth'
import {auth} from "./firebase"
import { emailAuth } from "./emailAuth";
import { useAuthState } from 'react-firebase-hooks/auth';

function App() {
  const [email, setEmail] = useState<string>("")
  const [user, loading] = useAuthState(auth)
  console.log(user?.email)

  const handleSignUp = () => {
    const message = emailAuth(email)
    console.log(message)
  }

  const handleLogOut = () => {
    signOut(auth).then(() => {
      console.log("signed out")
    }).catch((error) => {
      console.log(error)
    })
  }

  useEffect(() => {
    if(isSignInWithEmailLink(auth, window.location.href)) {
        let email = JSON.parse(localStorage.getItem('emailToSignUp') || '{}')
        if(!email) {
            email = window.prompt("Te rog adauga email-ul pentru confirmare.")
        }
        console.log(email.email)
        signInWithEmailLink(auth, email.email, window.location.href)
          .then((result) => {
              window.localStorage.removeItem('emailToSignUp')
              console.log(result.user, result)
          })
          .catch((error) => {
              console.log(error.code, error.message)
          })
    }
  }, [])

  return (
    <div className="App">
      <input type="email" placeholder="email" value={email} onChange={e => setEmail(e.target.value)} />
      <button onClick={handleSignUp}>Sign up</button>
      <button onClick={handleLogOut}>Log out</button>
    </div>
  );
}

export default App;
